import axios from 'axios';

/**
 * Busca médicos por nombre.
 * @param {string} search_term - Término de búsqueda.
 * @returns {Promise<Object>} Respuesta con los datos de los médicos.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const searchMedicos = async (search_term = '') => {
  try {
    const response = await axios.get('/api/medicos/buscar/nombre', {
      params: { search: search_term }
    });
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al buscar los médicos";
    throw new Error(message);
  }
};

/**
 * Obtiene todos los médicos.
 * @returns {Promise<Object>} Respuesta con los datos de los médicos.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const getAllMedicos = async () => {
  try {
    const response = await axios.get('/api/medicos');
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al obtener los médicos";
    throw new Error(message);
  }
};
