import axios from "@/config/axios";

// Crear una nueva cita
export const createCita = async (citaData) => {
  try {
    const response = await axios.post("/api/citas", citaData);
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};

// Obtener todas las citas
export const getAllCitas = async () => {
  try {
    const response = await axios.get("/api/citas");
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};

// Obtener una cita por ID
export const getCita = async (id) => {
  try {
    const response = await axios.get(`/api/citas/${id}`);
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};

// Actualizar una cita
export const updateCita = async (id, citaData) => {
  try {
    const response = await axios.put(`/api/citas/${id}`, citaData);
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};

// Eliminar una cita
export const deleteCita = async (id) => {
  try {
    const response = await axios.delete(`/api/citas/${id}`);
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};

// Obtener citas por mes y año
export const getCitasByMesAnio = async (mes, anio) => {
  try {
    const response = await axios.get(`/api/citas/mes-anio/consultar`, {
      params: {
        mes,
        anio
      }
    });
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};

// Actualizar estado de una cita
export const actualizarEstadoCita = async (id, estadoData) => {
  try {
    const response = await axios.post(`/api/citas/${id}/estado`, estadoData);
    return response.data;
  } catch (error) {
    throw error.response?.data || error;
  }
};
