import { useAuthStore } from "@/stores/authStore";

export function usePermissions() {
  const authStore = useAuthStore();

  /**
   * Verifica si el usuario tiene un permiso específico basado en el slug.
   * @param {string} permisoSlug - El slug del permiso a verificar.
   * @returns {boolean} - Verdadero si el permiso existe, falso en caso contrario.
   */
  const hasPermission = (permisoSlug) => {
    return authStore.permisos?.some((permiso) => permiso.slug === permisoSlug) || false;
  };

  return {
    hasPermission,
  };
}