<template>
    <div class="table-responsive">
        <a-table class="table border-0 custom-table comman-table datatable mb-0" :columns="columns"
            :row-key="record => record.id" :data-source="data.data" :pagination="{
                ...pagination,
                showSizeChanger: true, // Habilitar el selector de tamaño de página
                pageSizeOptions: ['10', '25', '50', '100', '200'], // Opciones de tamaño de página
            }" :loading="is_loading" @change="handleTableChange" :locale="{
                    triggerDesc: 'Orden descendente',
                    triggerAsc: 'Orden ascendente',
                    cancelSort: 'Cancelar orden'
                }">
            <template #bodyCell="{ column, record }">
                <template v-if="column.key === 'telefono'">
                    <div><a href="javascript:;">{{ record.telefono }}</a></div>
                </template>
                <template v-if="column.key === 'action'">
                    <div class="text-end">
                        <div class="dropdown dropdown-action">
                            <a href="javascript:;" class="action-icon dropdown-toggle" data-bs-toggle="dropdown"
                                aria-expanded="false">
                                <i class="fa fa-ellipsis-v"></i>
                            </a>
                            <div class="dropdown-menu dropdown-menu-end">
                                <a class="dropdown-item" href="javascript:;" @click="$emit('edit', record.id)">
                                    <i class="fa-solid fa-pen-to-square m-r-5"></i> Editar
                                </a>
                                <a class="dropdown-item" href="javascript:;" @click="handleDelete(record.id)">
                                    <i class="fa fa-trash-alt m-r-5"></i> Eliminar
                                </a>
                            </div>
                        </div>
                    </div>
                </template>
            </template>
        </a-table>
    </div>
</template>

<script setup>
import { ref } from "vue";
import Swal from "sweetalert2";
import { deletePatient } from "@/services/patientService";

const props = defineProps({
    columns: Array,
    data: Object,
    pagination: Object,
    is_loading: Boolean,
});

const emit = defineEmits(["change", "edit", "deleted"]);

const handleTableChange = (pagination, filters, sorter) => {
    emit("change", pagination, filters, sorter);
};

const handleDelete = async (id) => {
    const result = await Swal.fire({
        title: "¿Estás seguro?",
        text: "Esta acción no se puede deshacer",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Sí, eliminar",
        cancelButtonText: "Cancelar",
    });

    if (result.isConfirmed) {
        try {
            await deletePatient(id);
            Swal.fire("Eliminado", "El paciente ha sido eliminado con éxito.", "success");
            emit("listar"); // Emitir evento para actualizar la lista
        } catch (error) {
            Swal.fire("Error", error.message, "error");
        }
    }
};
</script>