<template>
  <div class="page-wrapper">
    <div class="content">
      <breadcrumb :title="title" :text="text" path="admin-dashboard" />

      <div class="good-morning-blk">
        <div class="row">
          <div class="col-md-6">
            <div class="morning-user">
              <h2>Hola, <span>{{ userName }}</span></h2>
              <p>Que tengas un buen día en el trabajo</p>
            </div>
          </div>
          <div class="col-md-6 position-blk">
            <div class="morning-img">
              <img src="@/assets/img/morning-img-01.png" alt="" />
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md-6 col-sm-6 col-lg-6 col-xl-3">
          <div class="dash-widget">
            <div class="dash-boxs comman-flex-center">
              <img src="@/assets/img/icons/profile-add.svg" alt="" />
            </div>
            <div class="dash-content dash-count">
              <h4>Médicos</h4>
              <h2 v-if="!cargandoEstadisticas">
                <vue3-autocounter
                  class="counter-up"
                  ref="counter"
                  :startAmount="0"
                  :delay="3"
                  :endAmount="estadisticas.total_medicos"
                  :duration="5"
                  separator=","
                  :autoinit="true"
                />
              </h2>
              <h2 v-else>
                <div class="spinner-border spinner-border-sm text-primary" role="status">
                  <span class="visually-hidden">Cargando...</span>
                </div>
              </h2>
              <p>
                <span class="passive-view"
                  ><i class="feather-arrow-up-right me-1"></i>Total</span
                >
                médicos registrados
              </p>
            </div>
          </div>
        </div>
        <div class="col-md-6 col-sm-6 col-lg-6 col-xl-3">
          <div class="dash-widget">
            <div class="dash-boxs comman-flex-center">
              <img src="@/assets/img/icons/profile-add.svg" alt="" />
            </div>
            <div class="dash-content dash-count">
              <h4>Aseguradoras</h4>
              <h2 v-if="!cargandoEstadisticas">
                <vue3-autocounter
                  class="counter-up"
                  ref="counter"
                  :startAmount="0"
                  :delay="3"
                  :endAmount="estadisticas.total_aseguradoras"
                  :duration="5"
                  separator=","
                  :autoinit="true"
                />
              </h2>
              <h2 v-else>
                <div class="spinner-border spinner-border-sm text-primary" role="status">
                  <span class="visually-hidden">Cargando...</span>
                </div>
              </h2>
              <p>
                <span class="passive-view"
                  ><i class="feather-arrow-up-right me-1"></i>Total</span
                >
                aseguradoras activas
              </p>
            </div>
          </div>
        </div>
        <div class="col-md-6 col-sm-6 col-lg-6 col-xl-3">
          <div class="dash-widget">
            <div class="dash-boxs comman-flex-center">
              <img src="@/assets/img/icons/calendar.svg" alt="" />
            </div>
            <div class="dash-content dash-count">
              <h4>Citas</h4>
              <h2 v-if="!cargandoEstadisticas">
                <vue3-autocounter
                  class="counter-up"
                  ref="counter"
                  :startAmount="0"
                  :delay="3"
                  :endAmount="estadisticas.total_citas"
                  :duration="5"
                  separator=","
                  :autoinit="true"
                />
              </h2>
              <h2 v-else>
                <div class="spinner-border spinner-border-sm text-primary" role="status">
                  <span class="visually-hidden">Cargando...</span>
                </div>
              </h2>
              <p>
                <span class="negative-view"
                  ><i class="feather-arrow-down-right me-1"></i>Total</span
                >
                citas programadas
              </p>
            </div>
          </div>
        </div>
        <div class="col-md-6 col-sm-6 col-lg-6 col-xl-3">
          <div class="dash-widget">
            <div class="dash-boxs comman-flex-center">
              <img src="@/assets/img/icons/empty-wallet.svg" alt="" />
            </div>
            <div class="dash-content dash-count">
              <h4>Pacientes</h4>
              <h2 v-if="!cargandoEstadisticas">
                <vue3-autocounter
                  class="counter-up"
                  ref="counter"
                  :startAmount="0"
                  :delay="3"
                  :endAmount="estadisticas.total_pacientes"
                  :duration="5"
                  separator=","
                  :autoinit="true"
                />
              </h2>
              <h2 v-else>
                <div class="spinner-border spinner-border-sm text-primary" role="status">
                  <span class="visually-hidden">Cargando...</span>
                </div>
              </h2>
              <p>
                <span class="passive-view"
                  ><i class="feather-arrow-up-right me-1"></i>Total</span
                >
                pacientes registrados
              </p>
            </div>
          </div>
        </div>
      </div>
      
      <div class="row">
       
        <div class="col-12 col-md-12 col-xl-12">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title d-inline-block">Citas del día</h4>
              <router-link to="/acitas-medicas" class="patient-views float-end"
                >Mostrar todo</router-link
              >
            </div>
            <div class="card-body p-0 table-dash">
              <div class="table-responsive">
                <upcoming-appointments></upcoming-appointments>
              </div>
            </div>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { useAuthStore } from '@/stores/authStore'
import { getEstadisticas } from '@/services/dashboardService'
import UpcomingAppointments from './upcoming-appointments.vue'

// Store
const authStore = useAuthStore()

// Breadcrumb data
const title = "Dashboard"
const text = "Dashboard"

// Computed
const userName = computed(() => authStore.user?.name || '')

// Reactive data
const estadisticas = ref({
  total_medicos: 0,
  total_pacientes: 0,
  total_citas: 0,
  total_aseguradoras: 0,
  total_especialidades: 0
})

const especialidades = ref([])
const cargandoEstadisticas = ref(true)

// Methods
const cargarEstadisticas = async () => {
  try {
    cargandoEstadisticas.value = true
    const response = await getEstadisticas()
    
    if (response.success) {
      estadisticas.value = {
        total_medicos: response.data.total_medicos || 0,
        total_pacientes: response.data.total_pacientes || 0,
        total_citas: response.data.total_citas || 0,
        total_aseguradoras: response.data.total_aseguradoras || 0,
        total_especialidades: response.data.total_especialidades || 0
      }
      
      especialidades.value = response.data.especialidades || []
    }
  } catch (error) {
    console.error('Error al cargar estadísticas:', error)
  } finally {
    cargandoEstadisticas.value = false
  }
}

// Lifecycle
onMounted(() => {
  cargarEstadisticas()
})
</script>
