<template>
  <teleport to="body">
    <div ref="modalRef" class="modal fade" tabindex="-1" role="dialog" data-bs-backdrop="static" data-bs-keyboard="false">
      <div :class="['modal-dialog', size]" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <slot name="header">
              <h5 class="modal-title">Título del Modal</h5>
            </slot>
            <button type="button" class="btn-close" @click="closeModal" aria-label="Close" v-if="!spinning"></button>
          </div>
          <div class="modal-body">
            <a-spin :spinning="spinning" size="large">
              <slot name="body">
                <p>Contenido del modal</p>
              </slot>
            </a-spin>
          </div>
          <div v-if="footer" class="modal-footer">
            <slot name="footer">
              <button type="button" class="btn btn-secondary" @click="closeModal">Cerrar</button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </teleport>
</template>

<script setup>
import { ref, defineExpose, defineProps, onMounted } from "vue";
import bootstrap from "bootstrap/dist/js/bootstrap.bundle.min.js";

const props = defineProps({
  size: {
    type: String,
    default: "modal-md",
  },
  footer: {
    type: Boolean,
    default: false, 
  },
  spinning: {
    type: Boolean,
    default: false,
  },
});

const modalRef = ref(null);
let modalInstance = null;

const openModal = () => {
  if (modalInstance) {
    modalInstance.show();
  }
};

const closeModal = () => {
  if (modalInstance) {
    modalInstance.hide();
  }
};


defineExpose({ openModal, closeModal });

onMounted(() => {
  if (modalRef.value) {
    modalInstance = new bootstrap.Modal(modalRef.value, { backdrop: "static" });
  }
});
</script>

<style scoped>
.modal.fade.show  {
  background-color: rgba(0, 0, 0, 0.7); /* Fondo negro con opacidad */
}
</style>