<template>
  <div class="container-fluid px-0">
    <div class="row">
      <!-- Login logo -->
      <div class="col-lg-6 login-wrap">
        <div class="login-sec">
          <div class="log-img">
            <img class="img-fluid" src="@/assets/img/login-02.png" alt="Logo" />
          </div>
        </div>
      </div>
      <!-- /Login logo -->

      <!-- Login Content -->
      <div class="col-lg-6 login-wrap-bg">
        <div class="login-wrapper">
          <div class="loginbox">
            <div class="login-right">
              <div class="login-right-wrap">
                <div class="account-logo">
                  <router-link to="dashboard"><img src="@/assets/img/login-logo.png" alt="" /></router-link>
                </div>
                <h2>Iniciar Sesión</h2>
                <!-- Form -->
                <Form @submit="onSubmit" :validation-schema="schema" v-slot="{ errors }">
                  <div class="input-block">
                    <label>Email <span class="login-danger">*</span></label>
                    <Field name="email" type="text" value="admin@admin.com" class="form-control"
                      :class="{ 'is-invalid': errors.email }" />
                    <div class="invalid-feedback">{{ errors.email }}</div>
                    <div class="emailshow text-danger" id="email"></div>
                  </div>
                  <div class="input-block">
                    <label>Password <span class="login-danger">*</span></label>
                    <Field name="password" :type="showPassword ? 'text' : 'password'" value="admin"
                      class="form-control pass-input mt-2" :class="{ 'is-invalid': errors.password }" />
                    <span @click="toggleShow" class="toggle-password">
                      <i :class="{
                        'fas fa-eye': showPassword,
                        'fas fa-eye-slash': !showPassword,
                      }"></i>
                    </span>
                    <div class="invalid-feedback">{{ errors.password }}</div>
                    <div class="emailshow text-danger" id="password"></div>
                  </div>

                  <div class="input-block login-btn">

                    <button class="btn btn-primary btn-block" type="submit" :disabled="loading">
                      <span class="spinner-border spinner-border-sm me-2" role="status" v-if="loading"></span>
                      {{ loading ? 'Procesando...' : 'Ingresar' }}
                    </button>

                  </div>


                </Form>
              
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- /Login Content -->
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";
import { login } from "@/services/authService";
import { useAuthStore } from "@/stores/authStore";
import swal from "sweetalert2"; // Importa swal directamente

const showPassword = ref(false);
const loading = ref(false);
const authStore = useAuthStore();
const router = useRouter();

// Define el esquema de validación
const schema = Yup.object().shape({
  email: Yup.string().required("El correo electrónico es requerido").email("Correo inválido"),
  password: Yup.string().required("La contraseña es requerida").min(5, "La contraseña debe tener al menos 6 caracteres"),
});

const onSubmit = async (values) => {
  try {
    loading.value = true;
    const data = await login(values.email, values.password);
    authStore.setAuthData(data.data);
    authStore.loadAuthData();
    await swal.fire("Bienvenido", "Bienvenido al sistema", "success"); // Usa swal directamente
    router.push("/dashboard");
  } catch (error) {
    console.error(error);
    await swal.fire("Error", error.message, "error"); // Usa swal directamente
  } finally {
    loading.value = false;
  }
};
</script>

<style scoped>
/* Agrega tus estilos aquí */
</style>
