import { createApp } from 'vue';
import { router } from '@/router';
import App from "@/App.vue";
import VCalendar from 'v-calendar';
import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/reset.css';
import esES from 'ant-design-vue/es/locale/es_ES';

import FlagIcon from 'vue-flag-icon';
import VueApexCharts from "vue3-apexcharts"
import VueSelect from 'vue3-select2-component'//Quitar
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'

import StarRating from 'vue-star-rating'
import VueFeather from 'vue-feather';
import Toaster from '@meforma/vue-toaster'
import Vue3Autocounter from 'vue3-autocounter';
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'


/********* Layouts components **********/
import Header from '@/views/layouts/header.vue'
import Sidebar from '@/views/layouts/sidebar.vue'
import SidebarItem from '@/views/layouts/sidebar-item.vue'
import Notifications from '@/components/notifications.vue'


/***** Breadcrumb  *****/
import Breadcrumb from '@/components/breadcrumb/breadcrumb.vue'


/******  Modal  ******/
import BaseModal from "@/components/modal/BaseModal.vue"; // Ruta del componente


import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import '@fortawesome/fontawesome-free/css/fontawesome.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import "vue3-circle-progress/dist/circle-progress.css";
import 'pe7-icon/dist/dist/pe-icon-7-stroke.css';
import 'typicons.font/src/font/typicons.css';
import 'weathericons/css/weather-icons.css'
import 'ionicons-npm/css/ionicons.css';
import './assets/css/sticky.css'
import "v-calendar/dist/style.css";
import '@/assets/css/feather.css';
import '@/assets/css/style.css';


import axios from './config/axios' 
import { createPinia } from 'pinia'


window.axios = axios
const pinia = createPinia()

const app = createApp(App)

/********* Layouts components **********/
app.component('layout-header', Header)
app.component('sidebar', Sidebar)
app.component('sidebar-item', SidebarItem)
app.component('notifications', Notifications)

/*****  Breadcrumb  *****/
app.component('breadcrumb', Breadcrumb)


  /*******  Modal *******/
app.component("BaseModal", BaseModal);

app.component('vue-select', VueSelect)
app.component('star-rating', StarRating)
app.component('datepicker', VueDatePicker)
app.component('vue3-autocounter', Vue3Autocounter)
app.use(VCalendar, {})
app.use(Toaster, {
  position: "top-right",
});
app.use(ElementPlus)
app.use(Antd, {
  locale: esES,
});
app.use(pinia)
app.component(VueFeather.name, VueFeather)
app.use(VueApexCharts)
app.use(FlagIcon)
app.use(router).mount('#app');


