import axios from 'axios';

/**
 * Obtiene la lista de tipos de documentos de identidad.
 * @returns {Promise<Object>} Respuesta con los datos de los tipos de documentos.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const getAllTypeDocuments = async () => {
  try {
    const response = await axios.get('/api/tipo-documento-identidad');
    return response.data; // Asumiendo que la respuesta incluye los datos necesarios
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al obtener los tipos de documentos de identidad";
    throw new Error(message);
  }
};