import axios from 'axios';

/**
 * Busca pacientes por nombre.
 * @param {string} search_term - Término de búsqueda.
 * @returns {Promise<Object>} Respuesta con los datos de los pacientes.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const searchPacientes = async (search_term = '') => {
    try {
        const response = await axios.get('/api/pacientes/buscar/nombre', {
            params: { search: search_term }
        });
        return response.data;
    } catch (error) {
        const message =
            error?.response?.data?.message ||
            error?.response?.data?.data?.message ||
            "Error al buscar los pacientes";
        throw new Error(message);
    }
};

export const getPatients = async (filters = {}) => {
    try {
        const response = await axios.get('/api/pacientes', {
            params: filters,
        });
        return response.data;
    } catch (error) {
        const message =
            error?.response?.data?.message || "Error al obtener los pacientes";
        throw new Error(message);
    }
};

export const getPatient = async (id) => {
    try {
        const response = await axios.get(`/api/pacientes/${id}`);
        return response.data;
    } catch (error) {
        const message =
            error?.response?.data?.message || "Error al obtener el paciente";
        throw new Error(message);
    }
};

export const createPatient = async (patientData) => {
    try {
        const response = await axios.post('/api/pacientes', patientData);
        return response.data;
    } catch (error) {
        const message =
            error?.response?.data?.message || "Error al crear el paciente";
        throw new Error(message);
    }
};

export const updatePatient = async (id, patientData) => {
    try {
        const response = await axios.put(`/api/pacientes/${id}`, patientData);
        return response.data;
    } catch (error) {
        const message =
            error?.response?.data?.message || "Error al actualizar el paciente";
        throw new Error(message);
    }
};

export const deletePatient = async (id) => {
    try {
        await axios.delete(`/api/pacientes/${id}`);
    } catch (error) {
        const message =
            error?.response?.data?.message || "Error al eliminar el paciente";
        throw new Error(message);
    }
};
