import axios from 'axios';

/**
 * Busca aseguradoras por nombre.
 * @param {string} search_term - Término de búsqueda.
 * @returns {Promise<Object>} Respuesta con los datos de las aseguradoras.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const searchAseguradoras = async (search_term = '') => {
  try {
    const response = await axios.get('/api/aseguradoras/buscar/nombre', {
      params: { search: search_term }
    });
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al buscar las aseguradoras";
    throw new Error(message);
  }
};

/**
 * Obtiene todas las aseguradoras.
 * @returns {Promise<Object>} Respuesta con los datos de las aseguradoras.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const getAllAseguradoras = async () => {
  try {
    const response = await axios.get('/api/aseguradoras');
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al obtener las aseguradoras";
    throw new Error(message);
  }
};
