<template>
  <div v-if="cargandoCitas" class="text-center py-4">
    <div class="spinner-border text-primary" role="status">
      <span class="visually-hidden">Cargando...</span>
    </div>
  </div>
  <table v-else class="table mb-0 border-0 datatable custom-table">
    <thead>
      <tr>
        <th>No</th>
        <th>Paciente</th>
        <th>Médico</th>
        <th>Hora</th>
        <th>Estado</th>
        <th>Motivo</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(cita, index) in citasDelDia" :key="cita.id">
        <td>{{ index + 1 }}</td>
        <td>
          <div class="table-avatar">
            <span class="avatar-text">{{ getInitials(cita.paciente) }}</span>
            <h2>{{ cita.paciente }}</h2>
          </div>
        </td>
        <td class="table-image appoint-doctor">
          <div class="avatar-text-small">Dr</div>
          <h2>Médico Asignado</h2>
        </td>
        <td class="appoint-time">
          <span>{{ fechaCitas }}</span>
          {{ cita.hora_inicio }} - {{ cita.hora_fin }}
        </td>
        <td>
          <button class="custom-badge status-green">
            Programada
          </button>
        </td>
        <td>{{ cita.motivo || 'Consulta general' }}</td>
      </tr>
      <tr v-if="citasDelDia.length === 0">
        <td colspan="6" class="text-center text-muted py-4">
          No hay citas programadas para hoy
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { getCitasDelDia } from '@/services/dashboardService'

// Reactive data
const citasDelDia = ref([])
const fechaCitas = ref('')
const totalCitas = ref(0)
const cargandoCitas = ref(true)

// Methods
const cargarCitasDelDia = async () => {
  try {
    cargandoCitas.value = true
    const response = await getCitasDelDia()
    
    if (response.success && response.data) {
      citasDelDia.value = response.data.citas || []
      fechaCitas.value = formatearFecha(response.data.fecha)
      totalCitas.value = response.data.total_citas || 0
    }
  } catch (error) {
    console.error('Error al cargar citas del día:', error)
  } finally {
    cargandoCitas.value = false
  }
}

const getInitials = (nombre) => {
  if (!nombre) return 'N/A'
  return nombre.split(' ')
    .map(n => n[0])
    .join('')
    .substring(0, 2)
    .toUpperCase()
}

const formatearFecha = (fecha) => {
  if (!fecha) return ''
  const fechaObj = new Date(fecha + 'T00:00:00') // Agregar tiempo para evitar problemas de zona horaria
  return fechaObj.toLocaleDateString('es-ES', {
    weekday: 'long',
    day: '2-digit',
    month: 'long'
  })
}

const getEstadoClass = (estado) => {
  switch (parseInt(estado)) {
    case 1:
      return 'status-yellow' // Pendiente
    case 2:
      return 'status-green'  // Confirmada
    case 3:
      return 'status-red'    // Cancelada
    default:
      return 'status-gray'
  }
}

const getEstadoTexto = (estado) => {
  switch (parseInt(estado)) {
    case 1:
      return 'Pendiente'
    case 2:
      return 'Confirmada'
    case 3:
      return 'Cancelada'
    default:
      return 'Sin estado'
  }
}

// Lifecycle
onMounted(() => {
  cargarCitasDelDia()
})
</script>

<style scoped>
.avatar-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #e9ecef;
  color: #495057;
  font-weight: bold;
  font-size: 12px;
  margin-right: 10px;
}

.avatar-text-small {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: #007bff;
  color: white;
  font-weight: bold;
  font-size: 10px;
  margin-right: 8px;
}

.table-avatar {
  display: flex;
  align-items: center;
}

.custom-badge {
  border: none;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 500;
}

.status-yellow {
  background-color: #fff3cd;
  color: #856404;
}

.status-green {
  background-color: #d1edff;
  color: #0c5460;
}

.status-red {
  background-color: #f8d7da;
  color: #721c24;
}

.status-gray {
  background-color: #e9ecef;
  color: #495057;
}
</style>
