<template>
  <div class="header">
    <div class="header-left">
      <router-link to="/dashboard" class="logo">
        <img src="@/assets/img/logo.png" width="35" height="35" alt="" />
        <span>Pre Clinic</span>
      </router-link>
    </div>
    <a id="toggle_btn" href="javascript:void(0);" @click="toggleSidebar">
      <img src="@/assets/img/icons/bar-icon.svg" alt="" />
    </a>
    <a id="mobile_btn" @click="toggleSidebarOne" class="mobile_btn float-start" href="#sidebar">
      <img src="@/assets/img/icons/bar-icon.svg" alt="" />
    </a>
   <!--  <div class="top-nav-search mob-view">
      <form>
        <input type="text" class="form-control" placeholder="Search here" />
        <a class="btn">
          <img src="@/assets/img/icons/search-normal.svg" alt="" />
        </a>
      </form>
    </div> -->
    <ul class="nav user-menu float-end">
      <li class="nav-item dropdown has-arrow user-profile-list">
        <a href="javascript:;" class="dropdown-toggle nav-link user-link" data-bs-toggle="dropdown">
          <div class="user-names">
            <h5>{{ user_name }}</h5>
            <span>{{ rol }}</span>
          </div>
          <span class="user-img">
            <img src="@/assets/img/user-06.jpg" alt="Admin" />
          </span>
        </a>
        <div class="dropdown-menu">
          <router-link class="dropdown-item" to="/profile">Mi Perfil</router-link>
          <router-link class="dropdown-item" to="/edit-profile">Editar Perfil</router-link>
          <router-link class="dropdown-item" to="/settings/settings">Configuración</router-link>
          <a class="dropdown-item" href="javascript:void(0);" @click="logoutHandler">Salir</a>
        </div>
      </li>
      <!-- <li class="nav-item">
        <router-link to="/settings/settings" class="hasnotifications">
          <img src="@/assets/img/icons/setting-icon-01.svg" alt="" />
        </router-link>
      </li> -->
    </ul>
    <div class="dropdown mobile-user-menu float-end">
      <a href="javascript:;" class="dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
        <i class="fa-solid fa-ellipsis-vertical"></i>
      </a>
      <div class="dropdown-menu dropdown-menu-end">
        <router-link class="dropdown-item" to="/profile">Mi Perfil</router-link>
        <router-link class="dropdown-item" to="/edit-profile">Editar Perfil</router-link>
        <router-link class="dropdown-item" to="/settings/settings">Configuración</router-link>
        <a class="dropdown-item" href="javascript:void(0);" @click="logoutHandler">Salir</a>
      </div>
    </div>
  </div>

  <notifications></notifications>

  <div ref="sidebarOverlay" class="sidebar-overlay" data-reff="#sidebar"></div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, computed } from "vue";
import { useAuthStore } from '../../stores/authStore';
import { useRouter } from "vue-router";
import Swal from 'sweetalert2';
import { logout as logoutService } from "@/services/authService"; // Importa el servicio

const isSidebarOpen = ref(false);
const sidebarOverlay = ref(null);

const router = useRouter();
const authStore = useAuthStore();

const user_name = computed(() => authStore.user?.name || '');
const rol = computed(() => authStore.user?.rol?.nombre || '');

const toggleSidebar = () => {
  isSidebarOpen.value = !isSidebarOpen.value;
  document.documentElement.classList.toggle("mini-sidebar");
};

const toggleSidebarOne = () => {
  isSidebarOpen.value = !isSidebarOpen.value;
  const mainWrapper = document.querySelector(".main-wrapper");

  if (isSidebarOpen.value) {
    sidebarOverlay.value.classList.add("opened");
    mainWrapper.classList.add("slide-nav");
  } else {
    sidebarOverlay.value.classList.remove("opened");
    mainWrapper.classList.remove("slide-nav", "open-msg-box");
  }

  document.documentElement.classList.toggle("menu-opened", isSidebarOpen.value);
};

const handleSidebarHover = () => {
  document.documentElement.classList.add("expand-menu");
};

const handleSidebarLeave = () => {
  document.documentElement.classList.remove("expand-menu");
};

const logoutHandler = async () => {
  try {
    await logoutService(); // Llama al servicio para cerrar sesión
    authStore.clearAuthData(); // Limpia el estado
    await Swal.fire({
      title: 'Desconectado',
      text: 'Has cerrado sesión',
      icon: 'success',
    });
    router.push('/login'); // Redirige al login
  } catch (error) {
    console.error('Error al cerrar sesión:', error);
    Swal.fire({
      title: 'Error',
      text: error.message || 'No se pudo cerrar sesión',
      icon: 'error',
    });
  }
};

onMounted(() => {
  authStore.loadAuthData();

  const sidebar = document.querySelector(".sidebar"); // Ajusta el selector según tu estructura HTML

  if (sidebar) {
    sidebar.addEventListener("mouseenter", handleSidebarHover);
    sidebar.addEventListener("mouseleave", handleSidebarLeave);
  }
});

onBeforeUnmount(() => {
  const sidebar = document.querySelector(".sidebar"); // Ajusta el selector según tu estructura HTML

  if (sidebar) {
    sidebar.removeEventListener("mouseenter", handleSidebarHover);
    sidebar.removeEventListener("mouseleave", handleSidebarLeave);
  }
});
</script>
