import axios from 'axios';

/**
 * Busca motivos de cita por nombre.
 * @param {string} search_term - Término de búsqueda.
 * @returns {Promise<Object>} Respuesta con los datos de los motivos.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const searchMotivos = async (search_term = '') => {
  try {
    const response = await axios.get('/api/motivos', {
      params: { search: search_term }
    });
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al buscar los motivos de cita";
    throw new Error(message);
  }
};

/**
 * Obtiene todos los motivos de cita.
 * @returns {Promise<Object>} Respuesta con los datos de los motivos.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const getAllMotivos = async () => {
  try {
    const response = await axios.get('/api/motivos');
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al obtener los motivos de cita";
    throw new Error(message);
  }
};
