<template>
  <BaseModal ref="modal_ref" :spinning="is_loading" size="modal-md">
    <template #header>
      <h4 class="modal-title">{{ modal_title }}</h4>
    </template>
    <template #body>
      <form>
        <div class="form-group">
          <label>Nombres <span class="text-danger">*</span></label>
          <input type="text" v-model="nombres" class="form-control" placeholder="Ingrese nombres" required />
          <div v-if="errors.nombres" class="text-danger">{{ errors.nombres }}</div>
        </div>
        <div class="form-group">
          <label>Apellido Paterno</label>
          <input type="text" v-model="apellido_paterno" class="form-control" placeholder="Ingrese apellido paterno" />
        </div>
        <div class="form-group">
          <label>Apellido Materno</label>
          <input type="text" v-model="apellido_materno" class="form-control" placeholder="Ingrese apellido materno" />
        </div>
        <div class="form-group">
          <label>Número de Documento <span class="text-danger">*</span></label>
          <input type="text" v-model="numero_documento_identidad" class="form-control" placeholder="Ingrese número de documento" required />
          <div v-if="errors.numero_documento_identidad" class="text-danger">{{ errors.numero_documento_identidad }}</div>
        </div>
        <div class="form-group">
          <label>Tipo de Documento <span class="text-danger">*</span></label>
          <el-select v-model="tipo_documento_identidad_id" placeholder="Seleccione un tipo de documento" filterable size="large" no-data-text="Sin datos" no-match-text="Sin coincidencias" required>
            <el-option v-for="tipo in tipos_documentos_list" :key="tipo.id" :label="tipo.nombre" :value="tipo.id"></el-option>
          </el-select>
          <div v-if="errors.tipo_documento_identidad_id" class="text-danger">{{ errors.tipo_documento_identidad_id }}</div>
        </div>
        <div class="form-group">
          <label>Fecha de Nacimiento</label>
          <datepicker v-model="fecha_nacimiento" :enable-time-picker="false" :editable="true" locale="es" :format="dateFormat" :clearable="true" placeholder="Fecha de nacimiento" cancelText="Cancelar" selectText="Seleccionar" />
        </div>
        <div class="form-group">
          <label>Teléfono</label>
          <input type="text" v-model="telefono" class="form-control" placeholder="Ingrese teléfono" />
        </div>
        <div class="form-group">
          <label>Email</label>
          <input type="email" v-model="email" class="form-control" placeholder="Ingrese email" />
        </div>
        <div class="m-t-20 text-center">
          <button type="button" class="btn btn-primary submit-btn" @click="handleSave">Guardar Paciente</button>
          <button type="button" class="btn btn-secondary submit-btn" @click="handleClose">Cerrar</button>
        </div>
      </form>
    </template>
  </BaseModal>
</template>

<script setup>
import { ref, computed, defineExpose, defineEmits } from "vue";
import * as Yup from "yup";
import Swal from "sweetalert2";
import { createPatient, getPatient, updatePatient } from "@/services/patientService";
import { getAllTypeDocuments } from "@/services/typeDocumentService";

const nombres = ref("");
const apellido_paterno = ref("");
const apellido_materno = ref("");
const numero_documento_identidad = ref("");
const tipo_documento_identidad_id = ref("");
const telefono = ref("");
const email = ref("");
const fecha_nacimiento = ref(null);
const errors = ref({});
const editing_patient_id = ref(null);
const tipos_documentos_list = ref([]);
const is_loading = ref(false);

const emit = defineEmits(["listar"]);

const modal_ref = ref(null);

const modal_title = computed(() =>
  editing_patient_id.value ? "Actualizar Paciente" : "Agregar Paciente"
);

const dateFormat = (date) => {
  const day = String(date.getDate()).padStart(2, "0");
  const month = String(date.getMonth() + 1).padStart(2, "0");
  const year = date.getFullYear();
  return `${day}-${month}-${year}`;
};

const resetForm = () => {
  nombres.value = "";
  apellido_paterno.value = "";
  apellido_materno.value = "";
  numero_documento_identidad.value = "";
  tipo_documento_identidad_id.value = null;
  telefono.value = "";
  email.value = "";
  fecha_nacimiento.value = null;
  errors.value = {};
  editing_patient_id.value = null;
};

const loadTypeDocuments = async () => {
  is_loading.value = true;
  try {
    const response = await getAllTypeDocuments();
    tipos_documentos_list.value = response.data;
  } catch (error) {
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar los tipos de documentos: " + error.message,
    });
  } finally {
    is_loading.value = false;
  }
};

const openModal = async (patient_id = null) => {
  resetForm();
  is_loading.value = true;
  modal_ref.value.openModal();
  await loadTypeDocuments();
  if (patient_id) {
    await loadPatientData(patient_id);
  }
  is_loading.value = false;
};

const handleClose = () => {
  resetForm();
  modal_ref.value.closeModal();
};

const schema = Yup.object().shape({
  nombres: Yup.string().required("Los nombres son obligatorios"),
  apellido_paterno: Yup.string().nullable(),
  apellido_materno: Yup.string().nullable(),
  numero_documento_identidad: Yup.string().required("Número de documento es obligatorio"),
  tipo_documento_identidad_id: Yup.number().required("El tipo de documento es obligatorio"),
  telefono: Yup.string().nullable().matches(/^\d{9}$/, "El teléfono debe tener 9 dígitos"),
  email: Yup.string().nullable().email("Formato de correo inválido"),
  fecha_nacimiento: Yup.date().nullable().max(new Date(), "La fecha no puede ser futura"),
});

const loadPatientData = async (patient_id) => {
  try {
    const { data } = await getPatient(patient_id);
    const patient = data;

    nombres.value = patient.nombres;
    apellido_paterno.value = patient.apellido_paterno;
    apellido_materno.value = patient.apellido_materno;
    numero_documento_identidad.value = patient.numero_documento_identidad;
    tipo_documento_identidad_id.value = patient.tipo_documento_identidad_id;
    telefono.value = patient.telefono;
    email.value = patient.email;
    fecha_nacimiento.value = patient.fecha_nacimiento;
    editing_patient_id.value = patient_id;
  } catch (error) {
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar los datos del paciente: " + error.message,
    });
  }
};

const handleSave = async () => {
  is_loading.value = true;
  try {
    const form_data = {
      nombres: nombres.value,
      apellido_paterno: apellido_paterno.value,
      apellido_materno: apellido_materno.value,
      numero_documento_identidad: numero_documento_identidad.value,
      tipo_documento_identidad_id: tipo_documento_identidad_id.value,
      telefono: telefono.value,
      email: email.value,
      fecha_nacimiento: fecha_nacimiento.value
        ? new Date(fecha_nacimiento.value).toISOString().split("T")[0]
        : null,
    };

    await schema.validate(form_data, { abortEarly: false });

    if (editing_patient_id.value) {
      await updatePatient(editing_patient_id.value, form_data);
      Swal.fire({
        icon: "success",
        title: "Éxito",
        text: "Paciente actualizado con éxito",
      });
    } else {
      await createPatient(form_data);
      Swal.fire({
        icon: "success",
        title: "Éxito",
        text: "Paciente creado con éxito",
      });
    }

    emit("listar");
    handleClose();
  } catch (err) {
    handleErrors(err);
  } finally {
    is_loading.value = false;
  }
};

const handleErrors = (err) => {
  if (err instanceof Yup.ValidationError) {
    const new_errors = {};
    err.inner.forEach((error) => {
      new_errors[error.path] = error.message;
    });
    errors.value = new_errors;
  } else {
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al guardar paciente",
    });
    console.error(err);
  }
};

defineExpose({ openModal });
</script>
