<template>
  <BaseModal ref="modal_ref" :spinning="is_loading" size="modal-lg">
    <template #header>
      <h4 class="modal-title">Detalles de la Cita</h4>
    </template>
    <template #body>
      <div v-if="cita" class="cita-details">
        <div class="row">
          <div class="col-md-6">
            <div class="info-item">
              <strong>📅 Fecha:</strong>
              <span>{{ cita.fecha }}</span>
            </div>
            <div class="info-item">
              <strong>🕐 Hora Inicio:</strong>
              <span>{{ cita.hora }}</span>
            </div>
            <div v-if="cita.hora_fin" class="info-item">
              <strong>🕐 Hora Fin:</strong>
              <span>{{ cita.hora_fin }}</span>
            </div>
            <div class="info-item">
              <strong>🏥 Especialidad:</strong>
              <span>{{ cita.especialidad }}</span>
            </div>
            <div class="info-item">
              <strong>👨‍⚕️ Médico:</strong>
              <span>{{ cita.medico }}</span>
            </div>
          </div>
          <div class="col-md-6">
            <div class="info-item">
              <strong>👤 Paciente:</strong>
              <span>{{ cita.paciente }}</span>
            </div>
            <div class="info-item">
              <strong>🏢 Aseguradora:</strong>
              <span>{{ cita.aseguradora }}</span>
            </div>
            <div class="info-item">
              <strong>📝 Motivo:</strong>
              <span>{{ cita.motivo }}</span>
            </div>
            <div class="info-item">
              <strong>📊 Estado:</strong>
              <span :class="getEstadoClass(cita.estado)">{{ cita.estado }}</span>
            </div>
          </div>
        </div>
        
        <div v-if="cita.detalle" class="row mt-3">
          <div class="col-12">
            <div class="info-item detalle-section">
              <strong>📋 Detalle:</strong>
              <div class="detalle-content">{{ cita.detalle }}</div>
            </div>
          </div>
        </div>

        <div class="row mt-4">
          <div class="col-12">
            <div class="action-buttons">
              <!-- Solo mostrar confirmar si está pendiente -->
              <button 
                v-if="cita.estado?.toLowerCase() === 'pendiente'"
                type="button" 
                class="btn btn-success me-2" 
                @click="confirmarCita"
                :disabled="is_loading"
              >
                ✅ Confirmar Cita
              </button>
              <!-- Solo mostrar cancelar si está pendiente o confirmada -->
              <button 
                v-if="cita.estado?.toLowerCase() === 'pendiente' || cita.estado?.toLowerCase() === 'confirmada'"
                type="button" 
                class="btn btn-danger me-2" 
                @click="cancelarCita"
                :disabled="is_loading"
              >
                ❌ Cancelar Cita
              </button>
              <!-- Editar siempre disponible excepto si está cancelada -->
              <button 
                v-if="cita.estado?.toLowerCase() !== 'cancelada'"
                type="button" 
                class="btn btn-primary me-2" 
                @click="editarCita"
                :disabled="is_loading"
              >
                ✏️ Editar
              </button>
              <button 
                type="button" 
                class="btn btn-secondary" 
                @click="cerrarModal"
                :disabled="is_loading"
              >
                Cerrar
              </button>
            </div>
          </div>
        </div>
      </div>
    </template>
  </BaseModal>
</template>

<script setup>
import { ref, defineEmits, defineExpose } from "vue";
import BaseModal from "@/components/modal/BaseModal.vue";
import Swal from "sweetalert2";
import { actualizarEstadoCita } from "@/services/citaService";

const emit = defineEmits(["listar", "editar"]);

const modal_ref = ref(null);
const is_loading = ref(false);
const cita = ref(null);

const getEstadoClass = (estado) => {
  switch (estado?.toLowerCase()) {
    case 'pendiente':
      return 'badge bg-warning text-white';
    case 'confirmada':
      return 'badge bg-success text-white';
    case 'cancelada':
      return 'badge bg-danger text-white';
    default:
      return 'badge bg-secondary text-white';
  }
};

const confirmarCita = async () => {
  try {
    const result = await Swal.fire({
      title: '¿Confirmar cita?',
      text: '¿Está seguro de que desea confirmar esta cita?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#28a745',
      cancelButtonColor: '#6c757d',
      confirmButtonText: 'Sí, confirmar',
      cancelButtonText: 'Cancelar'
    });

    if (result.isConfirmed) {
      is_loading.value = true;
      await actualizarEstadoCita(cita.value.id, { estado_id: 2 }); // 2 = Confirmada
      
      Swal.fire({
        icon: 'success',
        title: 'Éxito',
        text: 'Cita confirmada exitosamente',
        showConfirmButton: false,
        timer: 1500
      });

      // Actualizar el estado local de la cita
      cita.value.estado = 'Confirmada';
      
      // Emitir evento para recargar la lista
      emit('listar');
      cerrarModal();
    }
  } catch (error) {
    console.error('Error al confirmar cita:', error);
    Swal.fire({
      icon: 'error',
      title: 'Error',
      text: 'Error al confirmar la cita: ' + (error.message || 'Error desconocido'),
    });
  } finally {
    is_loading.value = false;
  }
};

const cancelarCita = async () => {
  try {
    const result = await Swal.fire({
      title: '¿Cancelar cita?',
      text: '¿Está seguro de que desea cancelar esta cita?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#dc3545',
      cancelButtonColor: '#6c757d',
      confirmButtonText: 'Sí, cancelar',
      cancelButtonText: 'No cancelar'
    });

    if (result.isConfirmed) {
      is_loading.value = true;
      await actualizarEstadoCita(cita.value.id, { estado_id: 3 }); // 3 = Cancelada
      
      Swal.fire({
        icon: 'success',
        title: 'Éxito',
        text: 'Cita cancelada exitosamente',
        showConfirmButton: false,
        timer: 1500
      });

      // Actualizar el estado local de la cita
      cita.value.estado = 'Cancelada';
      
      // Emitir evento para recargar la lista
      emit('listar');
      cerrarModal();
    }
  } catch (error) {
    console.error('Error al cancelar cita:', error);
    Swal.fire({
      icon: 'error',
      title: 'Error',
      text: 'Error al cancelar la cita: ' + (error.message || 'Error desconocido'),
    });
  } finally {
    is_loading.value = false;
  }
};

const editarCita = () => {
  emit('editar', cita.value.id);
  cerrarModal();
};

const cerrarModal = () => {
  cita.value = null;
  modal_ref.value.closeModal();
};

const openModal = (citaData) => {
  cita.value = citaData;
  modal_ref.value.openModal();
};

defineExpose({ openModal });
</script>

<style scoped>
.cita-details {
  padding: 20px 0;
}

.info-item {
  margin-bottom: 15px;
  padding: 12px;
  background-color: #f8f9fa;
  border-radius: 6px;
  border-left: 4px solid #007bff;
}

.info-item:nth-child(even) {
  background-color: #ffffff;
  border-left-color: #28a745;
}

.info-item strong {
  display: block;
  margin-bottom: 5px;
  color: #495057;
  font-size: 14px;
}

.info-item span {
  color: #2c3e50;
  font-weight: 500;
}

.detalle-section {
  border-left-color: #ffc107 !important;
}

.detalle-content {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #e9ecef;
  white-space: pre-wrap;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
  margin-top: 5px;
}

.action-buttons {
  text-align: center;
  padding: 20px 0;
  border-top: 1px solid #e9ecef;
}

.action-buttons .btn {
  margin: 5px;
  padding: 10px 20px;
  font-weight: 600;
}

.badge {
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 600;
  border-radius: 20px;
}

.bg-warning {
  background-color: #ffc107 !important;
  color: #212529;
}

.bg-success {
  background-color: #28a745 !important;
  color: white;
}

.bg-danger {
  background-color: #dc3545 !important;
  color: white;
}

.bg-secondary {
  background-color: #6c757d !important;
  color: white;
}

@media (max-width: 768px) {
  .action-buttons .btn {
    display: block;
    width: 100%;
    margin: 5px 0;
  }
  
  .info-item {
    margin-bottom: 10px;
    padding: 8px;
  }
}
</style>
