<template>
  <div class="page-wrapper">
    <div class="content">
      <!-- Page Header -->
      <breadcrumb :title="title" :text="text" path="pacientes" />
      <!-- /Page Header -->

      <div class="row">
        <div class="col-sm-12">
          <div class="card card-table show-entire">
            <div class="card-body">
              <!-- Table Header -->
              <div class="page-table-header mb-2">
                <div class="row align-items-center">
                  <div class="col">
                    <div class="doctor-table-blk">
                      <h3>Lista de pacientes</h3>
                      <div class="doctor-search-blk">
                        <div class="top-nav-search table-search-blk">
                          <form>
                            <input
                              type="text"
                              class="form-control"
                              placeholder="Buscar por nombre/documento"
                              v-model="search_name_documents"
                              @input="onSearch"
                            />
                            <a class="btn"> <img src="@/assets/img/icons/search-normal.svg" alt="" /> </a>
                          </form>
                        </div>
                        <div class="add-group">
                          <a href="javascript:;" class="btn btn-primary add-pluss ms-2" @click="openModal" v-if="hasPermission('crear-pacientes')">
                            <img src="@/assets/img/icons/plus.svg" alt="" />
                          </a>
                          <a href="javascript:;" class="btn btn-primary ms-2" @click="refreshTable">
                            <img src="@/assets/img/icons/re-fresh.svg" alt="" />
                          </a>
                          <a href="javascript:;" class="btn btn-primary ms-2" @click="clearSearch">
                            <img src="@/assets/img/icons/clear.svg" alt="" />
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-auto text-end float-end ms-auto download-grp">
                    <a href="javascript:;" class="me-2">
                      <img src="@/assets/img/icons/pdf-icon-01.svg" alt="" />
                    </a>
                    <a href="javascript:;" class="me-2">
                      <img src="@/assets/img/icons/pdf-icon-02.svg" alt="" />
                    </a>
                    <a href="javascript:;" class="me-2">
                      <img src="@/assets/img/icons/pdf-icon-03.svg" alt="" />
                    </a>
                    <a href="javascript:;">
                      <img src="@/assets/img/icons/pdf-icon-04.svg" alt="" />
                    </a>
                  </div>
                </div>
              </div>
              <!-- /Table Header -->

              <!-- Importar el componente de la tabla -->
              <PatientTable
                :columns="columns"
                :data="data"
                :pagination="pagination"
                :is_loading="is_loading"
                @change="handleTableChange"
                @edit="handleEdit"
                @listar="listarPacientes"
              />
            </div>
          </div>
        </div>
      </div>

      <Add ref="modal_ref" @listar="listarPacientes" />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from "vue";
import { usePermissions } from "@/composables/usePermissions";
import { getPatients } from "@/services/patientService";
import Add from './add.vue'; // Importa el componente Add
import PatientTable from "./table.vue"; // Importa el nuevo componente
import Swal from "sweetalert2";

let debounce_timeout = null; // Declarar debounce_timeout

const modal_ref = ref(null);
const title = ref("Pacientes");
const text = ref("Lista de pacientes");
const search_name_documents = ref("");
const is_loading = ref(false); // Indicador de carga para la tabla
const data = ref([]); // Datos de la tabla
const pagination = ref({
  current: 1, // Página actual
  page_size: 10, // Tamaño de página
  total: 0, // Total de registros
});

const { hasPermission } = usePermissions();

const columns = ref([
  {
    title: "Nombres",
    dataIndex: "nombres",
    key: "nombres",
    sorter: (a, b) => a.nombres.localeCompare(b.nombres),
  },
  {
    title: "Apellido Paterno",
    dataIndex: "apellido_paterno",
    key: "apellido_paterno",
    sorter: (a, b) => a.apellido_paterno.localeCompare(b.apellido_paterno),
  },
  {
    title: "Apellido Materno",
    dataIndex: "apellido_materno",
    key: "apellido_materno",
    sorter: (a, b) => a.apellido_materno.localeCompare(b.apellido_materno),
  },
  {
    title: "Número de Documento",
    dataIndex: "numero_documento_identidad",
    key: "numero_documento_identidad",
    sorter: (a, b) =>
      a.numero_documento_identidad.localeCompare(b.numero_documento_identidad),
  },
  {
    title: "Tipo de Documento",
    dataIndex: "tipo_documento_identidad",
    key: "tipo_documento_identidad",

  },
  {
    title: "Teléfono",
    dataIndex: "telefono",
    key: "telefono"
  },
  {
    title: "Email",
    dataIndex: "email",
    key: "email"
  },
  {
    title: "Fecha de Nacimiento",
    dataIndex: "fecha_nacimiento",
    key: "fecha_nacimiento",
    sorter: (a, b) => new Date(a.fecha_nacimiento) - new Date(b.fecha_nacimiento),
  },
  {
    title: "",
    key: "action",
    class: "text-end",
  },
]);

const listarPacientes = async (page = 1, page_size = 10, sort_field = null, sort_order = null) => {
  is_loading.value = true; // Activar el indicador de carga
  try {
    const filters = {
      search: search_name_documents.value, // Filtro por nombre/documento
      page,
      per_page: page_size,
    };

    // Agregar los parámetros de ordenamiento si están definidos
    if (sort_field && sort_order) {
      filters.sort_field = sort_field;
      filters.sort_order = sort_order === "ascend" ? "asc" : "desc";
    }

    const response = await getPatients(filters);
    data.value = response.data; // Asignar los datos obtenidos

    pagination.value = {
      current: data.value.meta.current_page,
      page_size: data.value.meta.per_page,
      total: data.value.meta.total,
    };
  } catch (error) {
    Swal.fire({
      icon: "error",
      title: "Error",
      text: error.message || "Error al cargar los pacientes",
    });
  } finally {
    is_loading.value = false; // Desactivar el indicador de carga
  }
};

const refreshTable = () => {
  listarPacientes(); // Actualizar la tabla
};

const openModal = () => {
  if (modal_ref.value) modal_ref.value.openModal();
};

const handleEdit = (id) => {
  if (modal_ref.value) {
    modal_ref.value.openModal(id); // Llamar al modal y pasar el ID del paciente
  }
};

// Función para manejar la búsqueda con debounce
const onSearch = () => {
  clearTimeout(debounce_timeout);
  debounce_timeout = setTimeout(() => {
    listarPacientes();
  }, 500); // Esperar 500ms antes de enviar la solicitud
};

const clearSearch = () => {
  search_name_documents.value = "";
  data.value = []; // Limpiar los datos de la tabla
  listarPacientes();
};

const handleTableChange = (pagination, filters, sorter) => {
  const sort_field = sorter.field;
  const sort_order = sorter.order;
  data.value.data = []
  // Llamar a listarPacientes con los parámetros de ordenamiento si están definidos
  listarPacientes(
    pagination.current,
    pagination.pageSize,
    sort_order ? sort_field : null,
    sort_order ? sort_order : null
  );
};

// Llamar a listarPacientes al montar el componente
onMounted(() => {
  listarPacientes();
});
</script>
