import { createRouter, createWebHistory } from 'vue-router';
import { useAuthStore } from '@/stores/authStore';
import Dashboard from '@/views/pages/dashboard/dashboard.vue';
import AdminDashboard from '@/views/pages/dashboard/admin-dashboard/admin-dashboard.vue';
import Login from '@/views/pages/pages/login.vue';
import Error_404 from '@/views/pages/pages/error-404.vue';
/* Vistas */
import Citas from '@/views/pages/citas/medical-appointments.vue';
import Patients from '@/views/pages/patients/patient.vue';

const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: { guest: true }
  },
  {
    path: '/citas-medicas',
    name: 'crear-empresa',
    component: Citas,
    meta: { requiresAuth: true }
  },
  {
    path: '/pacientes',
    name: 'ver-pacientes',
    component: Patients,
    meta: { requiresAuth: true }
  },
  {
    path: '/dashboard',
    component: Dashboard,
    meta: { requiresAuth: true },
    children: [
      //{ path: '', redirect: '/' },
      { path: '', name: 'admin-dashboard', component: AdminDashboard },
    ]
  },
  {
    path: '/error-404',
    name: 'error-404',
    component: Error_404
  },
  // Ruta comodín al final
  {
    path: '/:pathMatch(.*)*',
    name: 'not-found',
    component: Error_404
  }
];

export const router = createRouter({
  history: createWebHistory('/'),
  linkActiveClass: 'active',
  routes,
});

// Navegación global
router.beforeEach((to, from, next) => {
  const authStore = useAuthStore();
  authStore.loadAuthData();

  const token = authStore.token;

  if (to.meta.requiresAuth && !token) {
    // Si la ruta requiere autenticación y no hay token, redirige al login
    next('/login');
  } else if (to.meta.guest && token) {
    // Si está logueado e intenta ir a login o registro, redirige al dashboard
    next('/dashboard');
  } else {
    // Verifica permisos solo si requiere autenticación y no es el dashboard base
    const route_name = to.matched?.[to.matched.length - 1]?.name;

    if (to.meta.requiresAuth && route_name !== 'dashboard' && !to.path.startsWith('/dashboard')) {
      const has_permission = authStore.permisos?.some(permiso => permiso.slug === route_name);

      if (!has_permission) {
        next('/error-404');
        return;
      }
    }

    // Si la ruta no existe (not-found) y el usuario NO está logueado, redirige al login
    if (route_name === 'not-found' && !token) {
      next('/login');
      return;
    }

    next();
  }
});

export default router;