<template>
  <BaseModal ref="modal_ref" :spinning="is_loading" size="modal-md">
    <template #header>
      <h4 class="modal-title">{{ modal_title }}</h4>
    </template>
    <template #body>
      <form>
            <div class="form-group">
              <label>Paciente <span class="text-danger">*</span></label>
              <el-select 
                v-model="paciente_id" 
                placeholder="Seleccione un paciente" 
                filterable 
                remote
                :remote-method="handlePacienteSearch"
                :loading="is_loading_pacientes"
                required 
                size="large"
                no-data-text="Sin datos"
                no-match-text="Sin coincidencias"
              >
                <el-option v-for="paciente in pacientes" :key="paciente.id" :label="paciente.nombre_completo" :value="paciente.id"></el-option>
              </el-select>
              <div v-if="errors.paciente_id" class="text-danger">{{ errors.paciente_id }}</div>
            </div>
            <div class="form-group">
              <label>Aseguradora <span class="text-danger">*</span></label>
              <el-select 
                v-model="aseguradora_id" 
                placeholder="Seleccione una aseguradora" 
                filterable 
                remote
                :remote-method="handleAseguradoraSearch"
                :loading="is_loading_aseguradoras"
                required 
                size="large"
                no-data-text="Sin datos"
                no-match-text="Sin coincidencias"
              >
                <el-option v-for="aseguradora in aseguradoras" :key="aseguradora.id" :label="aseguradora.nombre" :value="aseguradora.id"></el-option>
              </el-select>
              <div v-if="errors.aseguradora_id" class="text-danger">{{ errors.aseguradora_id }}</div>
            </div>
            <div class="form-group">
              <label>Médico <span class="text-danger">*</span></label>
              <el-select 
                v-model="medico_id" 
                placeholder="Seleccione un médico" 
                filterable 
                remote
                :remote-method="handleMedicoSearch"
                :loading="is_loading_medicos"
                required 
                size="large"
                no-data-text="Sin datos"
                no-match-text="Sin coincidencias"
              >
                <el-option v-for="medico in medicos" :key="medico.id" :label="medico.nombre_completo" :value="medico.id"></el-option>
              </el-select>
              <div v-if="errors.medico_id" class="text-danger">{{ errors.medico_id }}</div>
            </div>
            <div class="form-group">
              <label>Especialidad <span class="text-danger">*</span></label>
              <el-select 
                v-model="especialidad_id" 
                placeholder="Seleccione una especialidad" 
                filterable 
                :loading="is_loading_especialidades"
                required 
                size="large"
                no-data-text="Sin datos"
                no-match-text="Sin coincidencias"
              >
                <el-option v-for="especialidad in especialidades" :key="especialidad.id" :label="especialidad.nombre" :value="especialidad.id"></el-option>
              </el-select>
              <div v-if="errors.especialidad_id" class="text-danger">{{ errors.especialidad_id }}</div>
            </div>
            <div class="form-group">
              <label>Motivo de la Cita <span class="text-danger">*</span></label>
              <el-select 
                v-model="motivo_cita_id" 
                placeholder="Seleccione un motivo" 
                filterable 
                :loading="is_loading_motivos"
                required 
                size="large"
                no-data-text="Sin datos"
                no-match-text="Sin coincidencias"
              >
                <el-option v-for="motivo in motivos" :key="motivo.id" :label="motivo.nombre" :value="motivo.id"></el-option>
              </el-select>
              <div v-if="errors.motivo_cita_id" class="text-danger">{{ errors.motivo_cita_id }}</div>
            </div>
            <div class="form-group">
              <label>Fecha <span class="text-danger">*</span></label>
              <div class="cal-icon">
                <datepicker v-model="fecha" :editable="true" locale="es" :format="dateFormat" :clearable="true" :enable-time-picker="false" placeholder="Seleccione la fecha" required />
              </div>
              <div v-if="errors.fecha" class="text-danger">{{ errors.fecha }}</div>
            </div>
            <div class="form-group">
              <label>Hora <span class="text-danger">*</span></label>
              <datepicker v-model="hora" type="time" time-picker locale="es" :clearable="true" :is-24="false" placeholder="Seleccione la hora" required />
              <div v-if="errors.hora" class="text-danger">{{ errors.hora }}</div>
            </div>
            <div class="form-group">
              <label>Hora Fin</label>
              <datepicker v-model="hora_fin" type="time" time-picker locale="es" :clearable="true" :is-24="false" placeholder="Seleccione la hora de fin" />
              <div v-if="errors.hora_fin" class="text-danger">{{ errors.hora_fin }}</div>
            </div>
            <div class="form-group">
              <label>Detalle</label>
              <textarea v-model="detalle" class="form-control" rows="3" placeholder="Ingrese detalles adicionales de la cita"></textarea>
              <div v-if="errors.detalle" class="text-danger">{{ errors.detalle }}</div>
            </div>
            <div class="m-t-20 text-center">
              <button type="button" class="btn btn-secondary submit-btn" @click="handleClose">Cerrar</button>
              <button type="button" class="btn btn-primary submit-btn" @click="handleCreate">
                {{ editing_cita_id ? 'Actualizar' : 'Registrar' }}
              </button>
            </div>
          </form>
        </template>
      </BaseModal>
    </template>

<script setup>
import { ref, computed, onMounted, defineEmits, defineExpose } from "vue";
import * as Yup from "yup";
import Swal from "sweetalert2";
import BaseModal from "@/components/modal/BaseModal.vue";
import { searchAseguradoras } from "@/services/aseguradoraService";
import { searchPacientes } from "@/services/patientService";
import { searchMedicos } from "@/services/medicoService";
import { getAllEspecialidades } from "@/services/especialidadService";
import { getAllMotivos } from "@/services/motivoCitaService";
import { createCita, updateCita, getCita } from "@/services/citaService";

const paciente_id = ref("");
const aseguradora_id = ref("");
const medico_id = ref("");
const especialidad_id = ref("");
const motivo_cita_id = ref("");
const fecha = ref(new Date());
const hora = ref({ hours: 0, minutes: 0, seconds: 0 });
const hora_fin = ref({ hours: 0, minutes: 0, seconds: 0 });
const detalle = ref("");
const errors = ref({});
const editing_cita_id = ref(null);
const is_loading = ref(false);

const emit = defineEmits(["listar"]);

const modal_ref = ref(null);

const modal_title = computed(() =>
  editing_cita_id.value ? "Actualizar Cita" : "Agregar Cita"
);

const dateFormat = (date) => {
  const day = String(date.getDate()).padStart(2, '0');
  const month = String(date.getMonth() + 1).padStart(2, '0'); // Los meses empiezan en 0
  const year = date.getFullYear();
  return `${day}-${month}-${year}`;
};

const pacientes = ref([]);
const aseguradoras = ref([]);
const medicos = ref([]);
const especialidades = ref([]);
const motivos = ref([]);

const is_loading_aseguradoras = ref(false);
const is_loading_pacientes = ref(false);
const is_loading_medicos = ref(false);
const is_loading_especialidades = ref(false);
const is_loading_motivos = ref(false);
let search_timeout = null;
let pacientes_timeout = null;
let medicos_timeout = null;

onMounted(() => {
  // BaseModal ya maneja la inicialización del modal internamente
});

// Función para cargar pacientes
const loadPacientes = async (search_term = '') => {
  is_loading_pacientes.value = true;
  try {
    const response = await searchPacientes(search_term);
    pacientes.value = response.data || [];
  } catch (error) {
    console.error("Error al cargar pacientes:", error.message);
    pacientes.value = [];
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar pacientes: " + error.message,
    });
  } finally {
    is_loading_pacientes.value = false;
  }
};

// Función para cargar médicos
const loadMedicos = async (search_term = '') => {
  is_loading_medicos.value = true;
  try {
    const response = await searchMedicos(search_term);
    medicos.value = response.data || [];
  } catch (error) {
    console.error("Error al cargar médicos:", error.message);
    medicos.value = [];
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar médicos: " + error.message,
    });
  } finally {
    is_loading_medicos.value = false;
  }
};

// Función para cargar especialidades como catálogo
const loadEspecialidades = async () => {
  is_loading_especialidades.value = true;
  try {
    const response = await getAllEspecialidades();
    especialidades.value = response.data || [];
  } catch (error) {
    console.error("Error al cargar especialidades:", error.message);
    especialidades.value = [];
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar especialidades: " + error.message,
    });
  } finally {
    is_loading_especialidades.value = false;
  }
};

// Función para cargar motivos como catálogo
const loadMotivos = async () => {
  is_loading_motivos.value = true;
  try {
    const response = await getAllMotivos();
    motivos.value = response.data || [];
  } catch (error) {
    console.error("Error al cargar motivos:", error.message);
    motivos.value = [];
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar motivos: " + error.message,
    });
  } finally {
    is_loading_motivos.value = false;
  }
};

// Función para cargar aseguradoras
const loadAseguradoras = async (search_term = '') => {
  is_loading_aseguradoras.value = true;
  try {
    const response = await searchAseguradoras(search_term);
    aseguradoras.value = response.data || [];
  } catch (error) {
    console.error("Error al cargar aseguradoras:", error.message);
    aseguradoras.value = [];
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar aseguradoras: " + error.message,
    });
  } finally {
    is_loading_aseguradoras.value = false;
  }
};

// Función para manejar la búsqueda de pacientes con debounce
const handlePacienteSearch = (search_term) => {
  // Solo buscar si hay un término de búsqueda
  if (!search_term || search_term.trim() === '') {
    pacientes.value = [];
    return;
  }
  
  // Limpiar el timeout anterior
  if (pacientes_timeout) {
    clearTimeout(pacientes_timeout);
  }
  
  // Establecer un nuevo timeout de 500ms
  pacientes_timeout = setTimeout(() => {
    loadPacientes(search_term);
  }, 500);
};

// Función para manejar la búsqueda de médicos con debounce
const handleMedicoSearch = (search_term) => {
  // Solo buscar si hay un término de búsqueda
  if (!search_term || search_term.trim() === '') {
    medicos.value = [];
    return;
  }
  
  // Limpiar el timeout anterior
  if (medicos_timeout) {
    clearTimeout(medicos_timeout);
  }
  
  // Establecer un nuevo timeout de 500ms
  medicos_timeout = setTimeout(() => {
    loadMedicos(search_term);
  }, 500);
};

// Función para manejar la búsqueda con debounce
const handleAseguradoraSearch = (search_term) => {
  // Solo buscar si hay un término de búsqueda
  if (!search_term || search_term.trim() === '') {
    aseguradoras.value = [];
    return;
  }
  
  // Limpiar el timeout anterior
  if (search_timeout) {
    clearTimeout(search_timeout);
  }
  
  // Establecer un nuevo timeout de 500ms
  search_timeout = setTimeout(() => {
    loadAseguradoras(search_term);
  }, 500);
};

// Esquema de validación
const schema = Yup.object().shape({
  paciente_id: Yup.number().required("Paciente es obligatorio"),
  aseguradora_id: Yup.number().required("Aseguradora es obligatoria"), 
  medico_id: Yup.number().required("Médico es obligatorio"),
  especialidad_id: Yup.number().required("Especialidad es obligatoria"),
  motivo_cita_id: Yup.number().required("Motivo de la cita es obligatorio"),
  fecha: Yup.date().required("Fecha es obligatoria").min(new Date().toDateString(), "La fecha no puede ser anterior a hoy"),
  hora: Yup.object().shape({
    hours: Yup.number().min(0).max(23).required("Hora es obligatoria"),
    minutes: Yup.number().min(0).max(59).required("Minutos son obligatorios"),
    seconds: Yup.number().min(0).max(59).required("Segundos son obligatorios"),
  }).required("Hora es obligatoria"),
  hora_fin: Yup.object().shape({
    hours: Yup.number().min(0).max(23),
    minutes: Yup.number().min(0).max(59),
    seconds: Yup.number().min(0).max(59),
  }).nullable(),
  detalle: Yup.string().max(500, "El detalle no puede exceder 500 caracteres"),
});

// Función para cargar los datos de una cita para edición
const loadCitaData = async (citaId) => {
  try {
    const response = await getCita(citaId);
    const cita = response.data;
    
    // Cargar datos en los formularios
    paciente_id.value = cita.paciente_id;
    aseguradora_id.value = cita.aseguradora_id;
    medico_id.value = cita.medico_id;
    especialidad_id.value = cita.especialidad_id;
    motivo_cita_id.value = cita.motivo_cita_id;
    // Convertir la fecha de "YYYY-MM-DD" a objeto Date
    if (cita.fecha) {
      const [year, month, day] = cita.fecha.split('-');
      fecha.value = new Date(Number(year), Number(month) - 1, Number(day));
    } else {
      fecha.value = new Date();
    }
    detalle.value = cita.detalle || "";
    
    // Parsear la hora de inicio
    if (cita.hora) {
      const [hours, minutes, seconds] = cita.hora.split(':');
      hora.value = {
        hours: parseInt(hours),
        minutes: parseInt(minutes),
        seconds: parseInt(seconds || 0)
      };
    }
    
    // Parsear la hora de fin si existe
    if (cita.hora_fin) {
      const [hours, minutes, seconds] = cita.hora_fin.split(':');
      hora_fin.value = {
        hours: parseInt(hours),
        minutes: parseInt(minutes),
        seconds: parseInt(seconds || 0)
      };
    } else {
      hora_fin.value = { hours: 0, minutes: 0, seconds: 0 };
    }
    
    // Cargar las opciones específicas para los selects remotos
    // Para paciente
    if (cita.paciente) {
      pacientes.value = [{
        id: cita.paciente_id,
        nombre_completo: cita.paciente.nombre_completo || cita.paciente
      }];
    }
    
    // Para aseguradora
    if (cita.aseguradora) {
      aseguradoras.value = [{
        id: cita.aseguradora_id,
        nombre: cita.aseguradora.nombre || cita.aseguradora
      }];
    }
    
    // Para médico
    if (cita.medico) {
      medicos.value = [{
        id: cita.medico_id,
        nombre_completo: cita.medico.nombre_completo || cita.medico
      }];
    }
    
  } catch (error) {
    console.error("Error al cargar datos de la cita:", error);
    Swal.fire({
      icon: "error",
      title: "Error",
      text: "Error al cargar los datos de la cita: " + (error.message || 'Error desconocido'),
    });
  }
};

// Función para restablecer el formulario
const resetForm = () => {
  paciente_id.value = "";
  aseguradora_id.value = "";
  medico_id.value = "";
  especialidad_id.value = "";
  motivo_cita_id.value = "";
  fecha.value = new Date();
  hora.value = { hours: 0, minutes: 0, seconds: 0 };
  hora_fin.value = { hours: 0, minutes: 0, seconds: 0 };
  detalle.value = "";
  errors.value = {};
  editing_cita_id.value = null;
  aseguradoras.value = []; // Limpiar lista de aseguradoras
  pacientes.value = []; // Limpiar lista de pacientes
  medicos.value = []; // Limpiar lista de médicos
  especialidades.value = []; // Limpiar lista de especialidades
  motivos.value = []; // Limpiar lista de motivos
};

// Exponer funciones para abrir y cerrar el modal desde el padre
const openModal = async (cita_id = null) => {
  resetForm();
  is_loading.value = true;
  modal_ref.value.openModal();
  
  // Cargar catálogos en paralelo usando Promise.all
  try {
    await Promise.all([
      loadEspecialidades(),
      loadMotivos()
    ]);
    if (cita_id) {
      // Cargar los datos de la cita para edición
      editing_cita_id.value = cita_id;
      await loadCitaData(cita_id);
    }
  } catch (error) {
    console.error("Error al cargar catálogos:", error);
  } finally {
    is_loading.value = false;
  }
};

const handleClose = () => {
  resetForm();
  modal_ref.value.closeModal();
};

const handleCreate = async () => {
  is_loading.value = true;
  
  const formData = {
    paciente_id: paciente_id.value,
    aseguradora_id: aseguradora_id.value,
    medico_id: medico_id.value,
    especialidad_id: especialidad_id.value,
    motivo_cita_id: motivo_cita_id.value,
    fecha: fecha.value,
    hora: hora.value,
    hora_fin: hora_fin.value,
    detalle: detalle.value,
  };

  try {
    await schema.validate(formData, { abortEarly: false });
    
    // Formatear los datos para el backend
    const citaData = {
      paciente_id: paciente_id.value,
      aseguradora_id: aseguradora_id.value,
      especialidad_id: especialidad_id.value,
      medico_id: medico_id.value,
      motivo_cita_id: motivo_cita_id.value,
      fecha: fecha.value.toISOString().split('T')[0], // Formato YYYY-MM-DD
      hora: `${String(hora.value.hours).padStart(2, '0')}:${String(hora.value.minutes).padStart(2, '0')}:${String(hora.value.seconds).padStart(2, '0')}`, // Formato HH:MM:SS
      hora_fin: hora_fin.value.hours !== 0 || hora_fin.value.minutes !== 0 || hora_fin.value.seconds !== 0 
        ? `${String(hora_fin.value.hours).padStart(2, '0')}:${String(hora_fin.value.minutes).padStart(2, '0')}:${String(hora_fin.value.seconds).padStart(2, '0')}`
        : null,
      detalle: detalle.value || null
    };

    if (editing_cita_id.value) {
      await updateCita(editing_cita_id.value, citaData);
      Swal.fire({
        icon: "success",
        title: "Éxito",
        text: "Cita actualizada con éxito",
        showConfirmButton: false,
        timer: 1500
      });
    } else {
      await createCita(citaData);
      Swal.fire({
        icon: "success",
        title: "Éxito", 
        text: "Cita creada con éxito",
        showConfirmButton: false,
        timer: 1500
      });
    }
    
    emit("listar");
    handleClose();
  } catch (err) {
    handleErrors(err);
  } finally {
    is_loading.value = false;
  }
};

const handleErrors = (err) => {
  if (err instanceof Yup.ValidationError) {
    const newErrors = {};
    err.inner.forEach((error) => {
      newErrors[error.path] = error.message;
    });
    errors.value = newErrors;
  } else {
    // Manejo de errores del backend
    let errorMessage = "Error al guardar cita";
    
    if (err?.message) {
      errorMessage = err.message;
    } else if (err?.data?.message) {
      errorMessage = err.data.message;
    } else if (err?.errors && typeof err.errors === 'object') {
      // Si son errores de validación del backend
      const backendErrors = {};
      Object.keys(err.errors).forEach(key => {
        if (Array.isArray(err.errors[key])) {
          backendErrors[key] = err.errors[key][0];
        } else {
          backendErrors[key] = err.errors[key];
        }
      });
      errors.value = backendErrors;
      return;
    }
    
    Swal.fire({
      icon: "error",
      title: "Error",
      text: errorMessage,
      confirmButtonText: "Aceptar"
    });
    console.error(err);
  }
};

// Permite que el componente padre pueda abrir el modal
defineExpose({ openModal });
</script>
