<template>
  <div class="sidebar" id="sidebar">
    <div class="sidebar-inner slimscroll">
      <perfect-scrollbar class="scroll-area" :settings="settings" @ps-scroll-y="scrollHandle">
        <div id="sidebar-menu" class="sidebar-menu">
          <sidebar-item></sidebar-item>
          <div class="logout-btn">
            <a href="javascript:void(0);" @click="handleLogout">
              <span class="menu-side">
                <img src="@/assets/img/icons/logout.svg" alt="" />
              </span>
              <span>Salir</span>
            </a>
          </div>
        </div>
      </perfect-scrollbar>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { useAuthStore } from "@/stores/authStore";
import { logout as logoutService } from "@/services/authService";
import Swal from "sweetalert2";
import { PerfectScrollbar } from "vue3-perfect-scrollbar";
import "vue3-perfect-scrollbar/dist/vue3-perfect-scrollbar.css";

// Configuración de PerfectScrollbar
const settings = ref({
  suppressScrollX: true,
});

// Instancias necesarias
const router = useRouter();
const authStore = useAuthStore();

// Manejo del scroll (si es necesario)
const scrollHandle = (evt) => {
  // Aquí puedes manejar eventos de scroll si es necesario
};

// Lógica para cerrar sesión
const handleLogout = async () => {
  try {
    await logoutService(); // Llama al servicio para cerrar sesión
    authStore.clearAuthData(); // Limpia el estado
    await Swal.fire({
      title: "Desconectado",
      text: "Has cerrado sesión",
      icon: "success",
    });
    router.push('/login');
  } catch (error) {
    console.error("Error al cerrar sesión:", error);
    Swal.fire({
      title: "Error",
      text: error.message || "No se pudo cerrar sesión",
      icon: "error",
    });
  }
};
</script>

