import axios from 'axios';

/**
 * Obtiene todas las especialidades.
 * @returns {Promise<Object>} Respuesta con los datos de las especialidades.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const getAllEspecialidades = async () => {
  try {
    const response = await axios.get('/api/especialidades');
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al obtener las especialidades";
    throw new Error(message);
  }
};

/**
 * Busca especialidades por nombre.
 * @param {string} search_term - Término de búsqueda.
 * @returns {Promise<Object>} Respuesta con los datos de las especialidades.
 * @throws {Error} Error con el mensaje correspondiente si la solicitud falla.
 */
export const searchEspecialidades = async (search_term = '') => {
  try {
    const response = await axios.get('/api/especialidades/buscar', {
      params: { search: search_term }
    });
    return response.data;
  } catch (error) {
    const message =
      error?.response?.data?.message ||
      error?.response?.data?.data?.message ||
      "Error al buscar las especialidades";
    throw new Error(message);
  }
};
